.wp-block-create-block-efipay-suscriptions {
	background-color: transparent;
	color: black;
	padding: 2px;
}


.plans-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    color: black;
    justify-content: center;
}

.plan-card {
    background-color: #f8f9fa;
    border: 1px solid #dee2e6;
    border-radius: 5px;
    padding: 20px;
    width: calc(33.33% - 20px); /* 33.33% for 3 cards per row with a gap of 20px */
}

.plan-name {
    font-size: 20px;
    margin-bottom: 10px;
}

.plan-description {
    font-size: 16px;
    color: #555;
    margin-bottom: 10px;
}

.plan-price {
    font-size: 18px;
    font-weight: bold;
    color: #007bff;
    margin-bottom: 10px;
}

.Button {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    text-decoration: none;
    display: inline-block;
    transition: background-color 0.3s;
}

.Button:hover {
    background-color: #0056b3;
}
